<script>
  import Factory from "@rdfjs/data-model";
  import Prefixplete from "prefixplete";
  import { onMount } from "svelte";

  export let value;

  let inputElement, prefixplete;

  onMount(() => {
    prefixplete = new Prefixplete(
      inputElement,
      "https://ontologies.farewellutopia.com/sparql",
      "https://ontologies.farewellutopia.com/"
    );

    prefixplete.action = function() {
      value = Factory.namedNode(prefixplete.value);
    };
    prefixplete.setValue(value.value);
  });

  function input(e) {
    value = Factory.namedNode(prefixplete.value);
  }
  $: if(prefixplete) {
     prefixplete.setValue(value.value);
  }
</script>

